<template>
  <!-- 组件化Cell -->
  <div>
    <div class="cell" v-on:click="onClickSelf">
      <template v-if="a">{{text}}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
export default {
  props: ["n",'finished'], //app中cell声明一个属性n，Cell组件中props接收
  data() {
    return { a: false, text: "" };
  },
  methods: {
    onClickSelf() {
      if (this.text !== "") {
        return;
      }
      if(this.finished){
          return;
      }
      this.a = true;
      this.text = this.n % 2 === 0 ? "x" : "o"; //在点击的时候，判断n，赋值text
      this.$emit("click",this.text); 
      //当cell被点击时，emit通知app，被点了,app中的click被触发
      //emit将点击的那个cell的text传给app，app中$event接收（$event接收emit传出的数据）
    }
  }
};
</script>

<style>
.cell {
  width: 100px;
  height: 100px;
  border: 1px solid rebeccapurple;
  font-size: 80px;
  text-align: center;
  line-height: 100px;
}
</style>
