<template>
  <div>
    <div>n的值为{{n}}</div>
    <div class="row">
      <!-- cellOnClick不会立即执行，只有在click的时候才会执行  原本的形式：<Cell @click="() => cellOnClick(0)" :n="n"/>  -->
      <Cell @click="cellOnClick(0,$event)" :n="n"  />
      <Cell @click="cellOnClick(1,$event)" :n="n" />
      <Cell @click="cellOnClick(2,$event)" :n="n" />
    </div>
    <div class="row">
      <Cell @click="cellOnClick(3,$event)" :n="n" />
      <Cell @click="cellOnClick(4,$event)" :n="n" />
      <Cell @click="cellOnClick(5,$event)" :n="n" />
    </div>
    <div class="row">
      <Cell @click="cellOnClick(6,$event)" :n="n" />
      <Cell @click="cellOnClick(7,$event)" :n="n" />
      <Cell @click="cellOnClick(8,$event)" :n="n" />
    </div>
    <div>{{map}}</div>
    <div>胜利者是：{{result}}</div>
  </div>
</template>

<script>
//组件化Cell
import Cell from "./Cell";
export default {
  components: { Cell },
  data() {
    return {
      n: 0,
      map: [[null, null, null], [null, null, null], [null, null, null]],
      result: "",
    };
  },
  //组件化Cell
  methods: {
    //当某cell被点，Cell组件会emit通知app，触发click，执行此函数
    cellOnClick(i, text) {
      //i是cell的序号
      // console.log(`${i}被点了，内容是${text}`);
      this.map[Math.floor(i / 3)][i % 3] = text;
      this.n += 1;
      this.tell();
    },
    tell() {
      //tell输出结果
      for (let i = 0; i < 2; i++) {
        if (
          this.map[i][0] !== null &&
          this.map[i][0] === this.map[i][1] &&
          this.map[i][1] === this.map[i][2]
        ) {
          this.result = this.map[i][0];
        }
      }
      for (let j = 0; j < 2; j++) {
        if (
          this.map[0][j] !== null &&
          this.map[0][j] === this.map[1][j] &&
          this.map[1][j] === this.map[2][j]
        ) {
          this.result = this.map[0][j];
        }
      }
      if (
        this.map[0][0] !== null &&
        this.map[0][0] === this.map[1][1] &&
        this.map[1][1] === this.map[2][2]
      ) {
        this.result = this.map[0][0];
      }
      if (
        this.map[0][2] !== null &&
        this.map[0][2] === this.map[1][1] &&
        this.map[1][1] === this.map[2][0]
      ) {
        this.result = this.map[0][2];
      }
    },
  }
};
</script>

<style>
.row {
  display: flex;
}
</style>
